<template>
  <DashboardContainer>
      <CountersGrid/>
      <GraphGrid1/>
      <GraphGrid2/>
  </DashboardContainer>
</template>

<script>
import DashboardContainer from '../containers/DashboardContainer'
import CountersGrid from '../components/dashboard/CountersGrid'
import GraphGrid1 from '../components/dashboard/GraphGrid1'
import GraphGrid2 from '../components/dashboard/GraphGrid2'
export default {
  name: 'Dashboard',
  beforeCreate: function () {
      var auth = this.$storage.get('auth')
      if (!auth) {
        this.$router.push('/login')
      }else{
        if(!auth.token){
          this.$router.push('/login')
        }
      }
  },
  components: {
    DashboardContainer,
    CountersGrid,
    GraphGrid1,
    GraphGrid2,
  }
}
</script>